import React, { useState, useEffect, useContext } from 'react'
// import { graphql } from "gatsby"
import AppContext from '../contextAPI/AppContext'
import scrollToElement from 'scroll-to-element'
import SpeechBubble from '../components/SpeechBubble/SpeechBubble'
import Narrator from '../components/NarratorText/NarratorText'
import Layout from "../components/Layout/layout"
import 'primereact/resources/themes/nova-light/theme.css';
import 'primereact/resources/primereact.min.css';
import 'primeicons/primeicons.css';

export default ({pageContext: {segment}, location}) => {
  let currentDisplayRef = false
  if (location.state !== null && location.state.currentDisplayRef !== undefined){
    currentDisplayRef = location.state.currentDisplayRef
  }
  const [ displayRef, updateDisplayRef ] = useState(currentDisplayRef)
  // This controls the popup for the word information
  const [ dialog, updateDialog ] = useState({open: false, content: {}})
  const newDialog = (obj) => {
    updateDialog(obj)
  }

  let scrollData = location.state
  useEffect(() => {
    if (typeof window !== undefined) {
      
      if (scrollData !== null && scrollData.book !== undefined) {
        updateDisplayRef(true)
        const id = `#${scrollData.book}-${scrollData.chapter}-${scrollData.verse}`
        scrollToElement(id, {
            offset: -55,
            ease: 'inOutCube',
            duration: 1500
        });
      }
    }
  })
  


  const [ interlinear, updateInterlinear ] = useState(false)
  const [ menu, updateMenu ] = useState(false)
  
  const prevURL = `/Reader/${segment.language}/${segment.version}/${segment.book}/${segment.chapter -1}`
  const nextURL = `/Reader/${segment.language}/${segment.version}/${segment.book}/${segment.chapter +1}`

  // convert string into JSON
  const contentJSON = JSON.parse(segment.content)

  return (
    <AppContext.Provider value={{dialog: dialog, newDialog: newDialog, displayRef: displayRef, updateDisplayRef: updateDisplayRef}}>
    <Layout 
      prevURL={prevURL} 
      nextURL={nextURL} 
      interlinear={() => updateInterlinear(!interlinear)}
      updateMenu={() => updateMenu(!menu)}
      menu={menu}
      version={segment.version}
    >
      {contentJSON.document.nodes.map(block => {
        const SVBData = {
          color: block.data.color,
          source: block.data.source
        }
        if (SVBData.color === 'black') {
          return (<Narrator 
            sourceData={SVBData}
            leaves={block.nodes[0].leaves}
            interlinear={interlinear} />
            )
        } else {
          return (<SpeechBubble 
            sourceData={SVBData}
            leaves={block.nodes[0].leaves}
            interlinear={interlinear} />
            )
        }
      })}
    </Layout>
    </AppContext.Provider>
  )}